.box-decide{
	padding: 48px 28px 48px 28px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	background: #FFFFFF;
	width: 100%;
	@media only screen and (min-width: 768px) {
		align-items: center;
	}
	& h4{
		font-style: normal;
		font-weight: 200;
		font-size: 28px;
		line-height: 34px;
		text-align: center;
		color: #030528;
		& span {
			font-style: normal;
			font-weight: 600;
			font-size: 28px;
			line-height: 34px;
			color: #D00561;
		}
	}
	& p {
		text-align: center;
		font-style: normal;
		font-weight: 200;
		font-size: 18px;
		line-height: 22px;
		color: #030528;
		margin: 24px 0 24px 0;
	}
	& form {
		display: flex;
		flex-direction: column;
		justify-content: center;
		@media only screen and (min-width: 768px) {
			align-items: center;
			flex-direction: row;
			flex-wrap: wrap;
			max-width: 456px;
		}
		& input {
			border: 1px solid #EBEBEB;
			border-radius: 6px;
			height: 36px;
			margin-bottom: 22px;
			padding: 8px 12px 8px 12px;
			&::placeholder{
				font-weight: 500;
				font-size: 14px;
				line-height: 140%;
				letter-spacing: -0.408px;
				
				color: #C8C8C8;
			}
			@media only screen and (min-width: 768px) {
				&:nth-of-type(1){
					margin-right: 16px;
				}
				width: 220px;
				height: 48px;
			}
		}
		button {
			margin-top: 10px;
			height: 40px;
			background: #D00561;
			border-radius: 6px;
			
			font-style: normal;
			font-weight: 500;
			font-size: 14px;
			line-height: 16px;
			text-align: center;
			
			color: #FFFFFF;
			@media only screen and (min-width: 768px) {
				margin-top: 34px;
				width: 96px;
				height: 40px;
			}
		}
	}
}