.section-works {
	background: #f7f9fb;
	display: flex;
	justify-content: center;
	&-container {
		padding: 110px 32px 0 32px;
		@media only screen and (min-width: 768px) {
			width: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			& h4 {
				margin-bottom: 45px !important;
			}
			&-box {
				margin: 0px 0 60px 0;
				display: flex;
				justify-content: center;
				flex-direction: row;
				flex-wrap: nowrap;
				align-items: center;
				&:nth-of-type(2),
				&:nth-of-type(4) {
					.section-works-container-box__item {
						order: 2;
						margin-right: 0px;
					}
					.section-works-container-box__image {
						display: flex;
						justify-content: flex-start;
						order: 1;
					}
				}
				&__item {
					max-width: 492px;
					height: 117px;
					& h5 {
						margin: 0px;
					}
					& p {
						margin: 0px;
						font-style: normal;
						font-weight: 400;
						font-size: 16px;
						line-height: 20px;
						color: #3e4954;
					}
				}
				&__image {
					display: flex;
					justify-content: flex-end;
					width: 564.67px;
					height: 352.57px;
					img {
						width: 100%;
						object-fit: contain;
					}
				}
			}
		}
		@media only screen and (min-width: 1366px) {
			&-box {
				margin: 0px 0 60px 0;
				display: flex;
				justify-content: center;
				flex-direction: row;
				flex-wrap: nowrap;
				align-items: center;
				&:nth-of-type(2),
				&:nth-of-type(4) {
					.section-works-container-box__item {
						order: 2;
						margin-right: 0px;
						margin-left: 150px;
					}
					.section-works-container-box__image {
						display: flex;
						justify-content: flex-start;
						order: 1;
					}
				}
				&__item {
					width: 492px;
					height: 117px;
					margin-right: 150px;
				}
				&__image {
					display: flex;
					justify-content: flex-end;
					width: 564.67px;
					height: 352.57px;
					img {
						width: 100%;
						object-fit: contain;
					}
				}
			}
		}

		h4 {
			margin-bottom: 56px;
			font-style: normal;
			font-weight: 200;
			font-size: 30px;
			line-height: 37px;
			color: #030528;
			span {
				font-weight: 600;
				color: #d00561;
			}
		}
		&-box {
			margin-bottom: 56px;
			&__item {
				&-number {
					display: block;
					font-style: normal;
					font-weight: 700;
					font-size: 16px;
					line-height: 20px;
					color: #d00561;
				}

				& h6 {
					font-style: normal;
					font-weight: 200;
					font-size: 24px;
					line-height: 29px;
					color: #030528;

					& span {
						font-weight: 500;
					}
				}

				& p {
					font-style: normal;
					font-weight: 300;
					font-size: 16px;
					line-height: 20px;
					margin-bottom: 32px;
				}
			}
			&__image {
				min-width: 311px;
				& img {
					width: 80%;
				}
			}
		}
	}
}
