.section-footer {
	background: #2e3f5f;
	@media only screen and (min-width: 768px) {
		display: flex;
		justify-content: center;
		flex-direction: column;
		align-items: center;
		flex-wrap: wrap;
	}
	&-container {
		padding: 64px 28px 64px 28px;
		@media only screen and (min-width: 768px) {
			padding: 64px 28px 64px 28px;
			display: flex;
			justify-content: space-between;
			max-height: 270px;
			max-width: 1210px;
			width: 100%;
		}
		@media only screen and (min-width: 1366px) {
			padding: 64px 0px 64px 0px;
		}

		&-logo {
			margin-bottom: 64px;
			display: flex;
		}

		&-items {
			&__nav {
				& ul {
					display: flex;
					flex-direction: column;
					margin-bottom: 48px;

					font-style: normal;
					font-weight: 100;
					font-size: 14px;
					line-height: 17px;
					color: rgba(255, 255, 255, 1);

					& li {
						margin-bottom: 16px;
						cursor: pointer;
					}
				}
			}

			&__contact {
				& ul {
					display: flex;
					flex-direction: column;
					margin-bottom: 48px;

					font-style: normal;
					font-weight: 100;
					font-size: 14px;
					line-height: 17px;
					color: rgba(255, 255, 255, 1);

					& li {
						margin-bottom: 16px;
						cursor: pointer;
					}
				}
			}

			&__social {
				display: flex;
				flex-direction: column;
				flex-wrap: nowrap;

				& span {
					font-style: normal;
					font-weight: 400;
					font-size: 14px;
					line-height: 17px;

					color: #ffffff;
				}

				& img {
					height: 48px;
					width: 48px;
					margin: 16px 8px 58px 0;
				}
			}

			&__download {
				display: flex;
				gap: 9px;
				@media only screen and (min-width: 768px) {
					gap: 6px;
				}
				img {
					width: 155px;
					height: 45px;
					margin-bottom: 10px;
					@media only screen and (min-width: 768px) {
						width: 90.86px;
						height: 26.72px;
						margin-bottom: 0px;
					}
				}
			}
		}
	}
	&-rights {
		padding: 0 24px 24px 24px;
		width: 100%;
		& hr {
			height: 1px;
			background: #c0c5cf;
			opacity: 0.2;
			margin-bottom: 24px;
		}

		& p {
			width: 300px;
			font-style: normal;
			font-weight: 200;
			font-size: 14px;
			line-height: 17px;
			height: 17px;
			color: #ffffff;
		}
		@media only screen and (min-width: 768px) {
			max-width: 1210px;
			padding: 0 24px 24px 24px;
		}
	}
}
