.section-addDishes {
	display: flex;
	justify-content: center;
	&-container {
		display: flex;
		justify-content: center;
		flex-direction: column;
		align-items: center;
		padding: 64px 16px 59px 16px;
		max-width: 768px;
		width: 100%;
		@media only screen and (min-width: 768px) {
			max-width: 1210px;
		}

		&-title {
			position: relative;
			width: 100%;
			@media only screen and (min-width: 768px) {
				width: 570px;
			}
			& h4 {
				font-weight: 300;
				font-size: 30px;
				line-height: 37px;
				color: #030528;
				text-align: center;
				margin-bottom: 24px;

				& span {
					font-weight: 600;
					font-size: 30px;
					line-height: 37px;
					color: #f50673;
				}
				@media only screen and (min-width: 768px) {
					font-weight: 300;
					font-size: 40px;
					line-height: 49px;
					margin-bottom: 23px;
					& span {
						font-style: normal;
						font-weight: 600;
						font-size: 40px;
						line-height: 49px;
					}
				}
			}

			& p {
				font-style: normal;
				font-weight: 300;
				font-size: 20px;
				line-height: 24px;
				text-align: center;
				color: #030528;

				& span {
					font-style: normal;
					font-weight: 600;
					font-size: 20px;
					line-height: 24px;
					color: #030528;
				}
				margin-bottom: 48px;
				@media only screen and (min-width: 768px) {
					font-weight: 300;
					font-size: 20px;
					line-height: 24px;
					& span {
						font-style: normal;
						font-weight: 600;
						font-size: 20px;
						line-height: 24px;
					}
					margin-bottom: 0px;
				}
			}
			& img {
				top: 45px;
				right: 10px;
				position: absolute;
				height: 42px;
				width: 42px;
				@media only screen and (min-width: 400px) {
					top: 25px;
				}
				@media only screen and (min-width: 768px) {
					top: 35px;
					right: -40px;
				}
			}
		}
		&-image {
			padding: 15px 0px 0 0px;
			width: 100%;
			min-width: 343px;
			min-height: 171.21px;
			height: auto;
			position: relative;
			display: flex;
			justify-content: center;
			&:after {
				content: '';
				background: {
					image: url('/assets/background-dishes.png');
					position: center;
					size: contain;
					repeat: no-repeat;
				}
				opacity: 0.09;
				top: 0;
				left: 0;
				bottom: 0;
				right: 0;
				position: absolute;
			}
			@media only screen and (min-width: 768px) {
				padding: 37px 16px 0 16px;
			}
			@media only screen and (min-width: 1366px) {
				width: 1210px;
				height: 598px;
			}
			& img {
				width: 100%;
				max-width: 1060px;
				max-height: 410px;
				@media only screen and (min-width: 1366px) {
					width: 1060px;
					height: 410px;
				}
			}
		}
	}
}
