.section-info {
	width: 100%;
	@media only screen and (min-width: 768px) {
		display: flex;
		justify-content: center;
		align-items: center;
	}
	@media only screen and (min-width: 1366px) {
		padding: 34px 20px 34px 20px;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	@media screen and (min-width: 1366px) {
		padding: 136px 80px 136px 80px;
	}
	&-container {
		padding: 77px 32px 73px 32px;
		background: #2e3f5f;
		display: flex;
		flex-direction: row;
		justify-content: center;
		flex-wrap: wrap;
		&__image {
			margin-top: 48px;
			max-width: 369px;
			@media only screen and (min-width: 1366px) {
				max-width: 569px;
				height: 592px;
			}
			& img {
				width: 100%;
			}
		}
		@media only screen and (min-width: 768px) {
			width: 100%;
		}
		@media only screen and (min-width: 1366px) {
			justify-content: flex-start;
			border-radius: 12px;
			width: 1210px;
			height: 496px;
			position: relative;
			&__image {
				position: absolute;
				right: -20px;
				bottom: -50px;
				width: 569px;
				height: 592px;
			}
		}
		&__text {
			& h5 {
				text-align: left;
				font-style: normal;
				font-weight: 300;
				font-size: 30px;
				line-height: 37px;
				color: #ffffff;
				& span {
					font-weight: 600;
					color: rgba(208, 5, 97, 1);
				}
			}
			& p {
				margin-top: 24px;
				font-style: normal;
				font-weight: 300;
				font-size: 16px;
				line-height: 20px;
				color: #f5f5f5;
			}
			@media only screen and (min-width: 768px) {
				max-height: 289px;
				max-width: 548px;
			}
		}
		button {
			background: #f5f5f5;
			align-items: flex-start;
			margin-top: 44px;
			color: #d00561;
			font-style: normal;
			font-weight: 500;
			font-size: 16px;
			line-height: 16px;
		}
	}
}
