.section-mobileApp {
	background: #fff6f7;
	display: flex;
	justify-content: center;
	@media only screen and (min-width: 1366px) {
		max-height: 763px;
	}
	&-container {
		display: flex;
		justify-content: center;
		flex-direction: row;
		align-items: center;
		flex-wrap: wrap;
		min-width: 375px;
		max-width: 1210px;
		padding: 0px 28px 0px 28px;
		@media only screen and (min-width: 1366px) {
			flex-wrap: nowrap;
		}
		&__title {
			max-width: 506px;
			& h4 {
				margin-top: 64px;
				font-style: normal;
				font-weight: 300;
				font-size: 30px;
				line-height: 37px;
				text-align: left;
				color: #030528;
				margin-bottom: 24px;
				& span {
					font-style: normal;
					font-weight: 600;
					font-size: 30px;
					line-height: 37px;
					color: #d00561;
				}
			}
			& p {
				font-style: normal;
				font-weight: 400;
				font-size: 16px;
				line-height: 20px;
				color: #030528;
				margin-bottom: 56px;
			}
			&-rating {
				max-width: 506px;
				display: flex;
				align-items: flex-start;
				justify-content: flex-start;
				flex-direction: row;
				& img {
					margin-right: 16px;
					height: 44px;
				}
				&__box {
					font-style: normal;
					font-weight: 500;
					font-size: 16px;
					line-height: 20px;
					color: #030528;
					&-small {
						display: flex;
						align-items: center;
						flex-direction: row;
						flex-wrap: nowrap;
						& img {
							margin-right: 4px;
							width: 24px;
							height: 24px;
						}
						& span {
							margin-right: 16px;
							font-weight: 300;
							font-size: 14px;
							line-height: 24px;
							letter-spacing: -0.408px;
							color: #030528;
						}
					}
				}
			}
			button {
				background: #d00561;
				border-radius: 6px;
				
				font-style: normal;
				font-weight: 500;
				font-size: 16px;
				line-height: 16px;
				text-align: center;
				
				color: #ffffff;
				
				margin-top: 56px;
				margin-bottom: 56px;
			}
		}
		&__image {
			display: flex;
			justify-content: center;
			align-content: center;
			max-width: 311px;
			margin-bottom: 56px;
			@media only screen and (min-width: 768px) {
				max-width: 512px;
			}
			@media only screen and (min-width: 1366px) {
				margin-bottom: 0px;
				max-width: 712px;
			}
		}
	}
}
