.navigation {
	display: flex;
	justify-content: center;
	background: #f6f3ec;
	&-container {
		max-width: 1210px;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		min-width: 375px;
		@media only screen and (min-width: 768px) and (max-width: 1365px) {
			min-width: 768px;
		}
		&-item {
			margin: 24px 16px 0 16px;
			height: 30px;
			display: flex;
			width: 100%;
			@media only screen and (min-width: 768px) {
				margin-top: 40px
			}

			&__logo {
				width: 20%;
				display: flex;
				justify-content: flex-start;
				align-items: center;
				a {
					display: flex;
					cursor: pointer;
					& .logoIcon {
						box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
						margin-right: 4px;
					}
				}
			}

			&__list {
				width: 80%;
				display: flex;
				justify-content: flex-end;
				gap: 25px;
				align-items: center;
				flex-direction: row;
				@media only screen and (min-width: 1366px) {
					gap: 77px;
				}
				&-item {
					display: none;
					justify-content: space-between;
					align-items: center;
					font-style: normal;
					font-weight: 500;
					font-size: 14px;
					line-height: 17px;
					color: #030528;
					@media only screen and (min-width: 768px) {
						font-weight: 400;
						font-size: 14px;
						line-height: 17px;
						display: flex;
						align-items: center;
						color: #030528;
					}
				}

				img {
					width: 32px;
					height: 32px;
					@media only screen and (min-width: 768px) {
						display: none;
					}
				}
			}
			&__title {
				display: flex;
				align-items: center;
				flex-direction: column;
				flex-wrap: nowrap;
				margin-top: 80px;
				height: 220px;
				width: 100%;
				& h1 {
					padding: 0px 44px 0px 44px;
					text-align: center;
					font-style: normal;
					font-weight: 300;
					font-size: 36px;
					line-height: 44px;
					color: #030528;
					text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
					& b {
						font-style: normal;
						font-weight: 700;
						font-size: 36px;
						line-height: 44px;
						color: #030528;
					}
					& span {
						font-style: normal;
						font-weight: 700;
						font-size: 36px;
						line-height: 44px;
						color: #d00561;
					}
					@media only screen and (min-width: 768px) {
						max-width: 549px;
					}
				}

				.btn {
					margin-top: 40px;
					background: #d00561;
					color: #ffffff;
					cursor: pointer;
				}
			}
		}
	}
}
