.section-benefits {
	display: flex;
	justify-content: center;
	min-width: 375px;
	&-container {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		background: #f0f9ff;
		max-width: 768px;
		@media only screen and (min-width: 768px) {
			background: none;
			max-width: 1210px;
		}

		@media only screen and (min-width: 1366px) {
			padding-top: 60px;
			padding-bottom: 29px;
			& h4 {
				margin-bottom: 44px !important ;
			}
		}
		& h4 {
			padding: 0px 10px 0 10px;
			font-style: normal;
			font-weight: 300;
			font-size: 30px;
			line-height: 37px;
			color: #030528;
			text-align: center;
			margin-top: 64px;
			margin-bottom: 56px;
			& span {
				font-style: normal;
				font-weight: 600;
				font-size: 30px;
				line-height: 37px;
				color: #d00561;
			}
		}

		&-box {
			background: #f0f9ff;
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			justify-content: center;
			align-content: center;
			align-items: center;
			gap: 40px;
			padding: 51px 0 51px 0;
			@media only screen and (min-width: 768px) {
				padding: 51px 50px 51px 50px;
			}
			@media only screen and (min-width: 1366px) {
				height: 496px;
				width: 100%;
			}
			&__items {
				width: 320px;
				padding: 0 28px 0 28px;
				background: #ffffff;
				border-radius: 12px;
				display: flex;
				justify-content: center;
				align-items: center;
				flex-direction: column;
				height: 290px;

				& img {
					width: 56px;
					height: 56px;
					margin-bottom: 16px;
				}

				& h5 {
					font-style: normal;
					font-weight: 500;
					font-size: 24px;
					line-height: 29px;
					color: #1a010c;
					margin-bottom: 8px;
				}

				& span {
					font-style: normal;
					font-weight: 400;
					font-size: 16px;
					line-height: 20px;
					color: #3c2f35;
					text-align: center;
				}

				& hr {
					margin-top: 16px;
					width: 20px;
					height: 1px;
				}
				&:nth-of-type(1) {
					border: 1px solid #27ae60;

					& hr {
						background-color: #27ae60;
					}
				}

				&:nth-of-type(2) {
					border: 1px solid #d00561;

					& hr {
						background-color: #d00561;
					}
				}

				&:nth-of-type(3) {
					border: 1px solid #2e3f5f;

					& hr {
						background-color: #2e3f5f;
					}
				}
			}
		}
		&-btn {
			margin-top: 8px;
			background-color: #ffffff;
			min-width: 375px;
			width: 100%;
			height: 122px;
			display: flex;
			align-items: center;
			justify-content: center;

			button {
				background: #d00561;
				font-style: normal;
				font-weight: 500;
				font-size: 16px;
				line-height: 16px;
				text-align: center;
				color: #ffffff;
			}
			@media only screen and (min-width: 1366px) {
				width: 1210px;
			}
		}
	}
}
