.section-faqs {
	background-color: #2e3f5f;
	display: flex;
	justify-content: center;
	@media only screen and (min-width: 1366px) {
		padding: 0 32px 30px 32px;
		justify-content: space-between;
		align-items: center;
	}
	&-background-right {
		display: none;
		background: {
			image: url('/assets/faq-background-right.png');
			repeat: no-repeat;
			position: center;
			size: contain;
		}
		width: 190.02px;
		height: 605.98px;
		@media only screen and (min-width: 1366px) {
			display: block;
		}
	}

	&-background-left {
		display: none;
		background: {
			image: url('/assets/faq-background-left.png');
			repeat: no-repeat;
			position: center;
			size: contain;
		}
		width: 190.02px;
		height: 605.98px;
		@media only screen and (min-width: 1366px) {
			display: block;
		}
	}

	&-container {
		margin: 64px 32px 64px 32px;
		max-width: 638px;
		& h4 {
			font-style: normal;
			font-weight: 300;
			font-size: 30px;
			line-height: 37px;
			text-align: center;

			color: #ffffff;
			margin-bottom: 24px;
		}

		& p {
			font-style: normal;
			font-weight: 200;
			font-size: 20px;
			line-height: 24px;
			text-align: center;

			color: #ffffff;
			margin-bottom: 56px;
		}

		&-items {
			width: 100%;

			&__box {
				width: 100%;
				display: flex;
				flex-direction: row;
				flex-wrap: nowrap;
				align-items: center;

				& img {
					width: 33px;
					height: 33px;
					margin-right: 25px;
				}

				&-img__big {
					display: none;
					@media only screen and (min-width: 768px) {
						display: block;
					}
				}

				&-message {
					min-width: 254px;
					width: 100%;
					padding-left: 16px;
					border-radius: 6px;
					display: flex;
					align-items: center;
					height: 70px;
					background: #ffffff;
					margin-bottom: 8px;
					position: relative;
					font-style: normal;
					font-weight: 500;
					font-size: 16px;
					line-height: 24px;
					color: #030528;
					@media only screen and (min-width: 768px) {
						width: 385px;
					}

					&::before {
						position: absolute;
						content: '';
						width: 12.73px;
						height: 12.73px;
						background: #ffffff;
						transform: rotate(45deg);
						left: -5px;
						top: calc(50% - 6px);
					}
				}

				&-message__small {
					min-width: 254px;
					width: 100%;
					padding-left: 16px;
					border-radius: 6px;
					display: flex;
					align-items: center;
					height: 48px;
					background: #ffffff;
					margin-bottom: 8px;
					position: relative;
					font-style: normal;
					font-weight: 500;
					font-size: 16px;
					line-height: 24px;
					color: #030528;
					@media only screen and (min-width: 768px) {
						width: 385px;
					}
					&::before {
						position: absolute;
						content: '';
						width: 12.73px;
						height: 12.73px;
						background: #ffffff;
						transform: rotate(45deg);
						left: -5px;
						top: calc(50% - 6px);
					}
				}

				&-message__big {
					margin: 8px 6.4px 16px 0;
					min-width: 303px;
					max-height: 176px;
					width: 100%;
					padding: 12px 20px 16px 12px;
					display: flex;
					align-items: center;
					background: #fedbeb;
					border-radius: 6px;
					height: 176px;
					position: relative;
					font-style: normal;
					font-weight: 400;
					font-size: 16px;
					line-height: 24px;
					color: #3e4954;
					@media only screen and (min-width: 768px) {
						max-width: 485px;
						max-height: 128px;
						padding: 16px 38px 16px 24px;
					}

					&::after {
						position: absolute;
						content: '';
						width: 12.73px;
						height: 12.73px;
						background: #fedbeb;
						transform: rotate(45deg);
						right: -5px;
						bottom: 15px;
					}
				}
				&:nth-of-type(2) {
					@media only screen and (min-width: 768px) {
						margin-right: 100px;
						display: flex;
						flex-direction: row-reverse;
						align-items: flex-end;
						& img {
							margin-bottom: 20px;
							margin-left: 10px;
							margin-right: 0px;
						}
					}
					@media only screen and (min-width: 1366px) {
						margin-right: 154px;
					}
				}
			}
		}
	}
}
