.section-feedbacks {
	background: #f6f3ec;
	display: flex;
	justify-content: center;
	flex-direction: column;
	@media only screen and (min-width: 1024px) {
		flex-wrap: nowrap;
		align-items: center;
	}
	&-container {
		min-width: 375px;
		display: flex;
		justify-content: center;
		flex-direction: row;
		flex-wrap: wrap;
		max-width: 1210px;
		padding: 48px 28px 48px 28px;
		@media only screen and (min-width: 1024px) {
			justify-content: space-between;
			padding: 48px 0px 48px 0px;
			align-items: center;
		}
		&__title {
			@media only screen and (min-width: 1024px) {
				max-width: 468px;
				max-height: 277px;
			}
			& h4 {
				padding: 0 28px 0 28px;
				font-style: normal;
				font-weight: 300;
				font-size: 28px;
				line-height: 34px;

				color: #030528;
				margin-bottom: 44px;

				& span {
					font-style: normal;
					font-weight: 600;
					font-size: 28px;
					line-height: 34px;
					color: #d00561;
				}
			}

			&-massage {
				& figure {
					display: flex;
					height: 48px;
					margin-bottom: 16px;

					& img {
						width: 48px;
						height: 48px;
					}

					& figcaption {
						height: 40px;
						margin-left: 16px;
						display: flex;
						align-content: center;
						flex-direction: row;
						flex-wrap: wrap;

						& span {
							width: 100%;
						}

						& img {
							width: 15px;
							height: 16px;
						}
					}
				}

				& div {
					background: #ffffff;
					border-radius: 6px;
					padding: 24px;
					margin-bottom: 64px;
					@media only screen and (min-width: 1024px) {
						height: 120px;
					}
					& p {
						font-style: normal;
						font-weight: 400;
						font-size: 15px;
						line-height: 20px;
						color: #1a010c;
					}
				}
			}
		}
		&__picture {
			padding: 0 28px 0 28px;
			margin-bottom: 44px;
			@media only screen and (min-width: 1024px) {
				margin-bottom: 0px;
				padding: 0px;
			}
			& picture {
				width: 319px;
				@media only screen and (min-width: 1024px) {
					max-width: 481px;
				}
				img {
					width: 100%;
					@media only screen and (min-width: 1024px) {
						width: 480px;
					}
					@media only screen and (min-width: 1366px) {
						width: 591px;
					}
				}
			}
		}
		&__slider {
			margin-top: 23px;
			display: flex;
			justify-content: center;
			align-items: center;
			width: 100%;
			height: 32px;
			&-arrowLeft {
				width: 24px;
				height: 24px;
				background-position: center;
				background-repeat: no-repeat;
				display: flex;
				justify-content: center;
				align-items: center;
				margin-right: 36px;
				cursor: pointer;
				@media only screen and (min-width: 1024px) {
					margin-right: 32px;
				}
				svg {
					fill: #fdb6d7;
					& :hover {
						fill: #d00561;
					}
				}
			}
			&-number {
				display: flex;
				flex-direction: row;
				flex-wrap: nowrap;
				align-content: center;
				gap: 8px;
				& span {
					cursor: pointer;
					display: flex;
					justify-content: center;
					align-items: center;
					border: 1px solid #c8c8c8;
					color: #c8c8c8;
					border-radius: 2px;
					width: 24px;
					height: 24px;
					&:hover {
						border: 1px solid #d00561;
						border-radius: 2px;
						color: #d00561;
					}
				}
			}
			&-arrowRight {
				cursor: pointer;
				width: 24px;
				height: 24px;
				background-position: center;
				background-repeat: no-repeat;
				display: flex;
				justify-content: center;
				align-items: center;
				margin-left: 36px;
				@media only screen and (min-width: 1024px) {
					margin-left: 32px;
				}
				svg {
					fill: #fdb6d7;
					& :hover {
						fill: #d00561;
					}
				}
			}
			&-activeSvg {
				fill: #d00561 !important;
			}
			&-activeNum {
				color: #d00561 !important;
				border: 1px solid #d00561 !important;
			}
			@media only screen and (min-width: 1024px) {
				margin-top: 5px;
				margin-left: 18px ;
				max-width: 248px;
			}
			@media only screen and (min-width: 1366px) {
				margin-top: -25px;
				margin-left: 18px ;
				max-width: 248px;
			}
		}
	}
}
