.card {
	margin-top: 40px;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	min-width: 375px;
	@media only screen and (min-width: 768px) {
		flex-direction: column-reverse;
		margin-top: 0px;
		margin-bottom: 43px;
	}
	@media only screen and (min-width: 1366px) {
		margin-top: -64px;
	}
	&-container {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;
		gap: 11px;
		&-box {
			display: flex;
			border-radius: 10px;
			height: 175px;
			width: 170px;
			overflow: hidden;
			@media only screen and (min-width: 768px) {
				width: 293px;
				height: 301px;
			}
		}

		&-one {
			display: flex;
			justify-content: center;
			flex-direction: row;
			flex-wrap: wrap;
			background: {
				color: #fedbeb;
				image: url('/assets/backgroundOneCard.svg');
				position: center;
				repeat: no-repeat;
				size: cover;
			}
			z-index: 1;
			position: relative;
			& img {
				margin-top: 5px;
				filter: drop-shadow(3px 6px 28px rgba(0, 0, 0, 0.28));
				position: absolute;
				height: 130px;
				top: 12px;
				z-index: 2;
				@media only screen and (min-width: 768px) {
					margin-top: 34px;
					height: 185px;
				}
			}
			& h4 {
				position: absolute;
				z-index: 3;
				color: #030528;
				opacity: 0.2;
				font-size: 13px;
				bottom: 10px;
				@media only screen and (min-width: 768px) {
					font-size: 20px;
					bottom: 30px;
				}
			}
		}

		&-two {
			z-index: 1;
			background: #ffd27d;
			position: relative;
			justify-content: center;
			& img {
				margin-top: 5px;
				filter: drop-shadow(0px 1px 19px rgba(0, 0, 0, 0.11));
				border-radius: 8px 8px 0px 0px;
				position: absolute;
				display: flex;
				justify-content: center;
				height: 155px;
				top: 15px;
				z-index: 2;
				@media only screen and (min-width: 768px) {
					margin-top: 31px;
					height: 255px;
				}
			}
		}

		&-three {
			background: {
				color: #daf5e1;
				image: url('/assets/card3.svg');
				position: 0 75%;
				repeat: no-repeat;
				size: 175px;
			}
			z-index: 1;
			display: flex;
			flex-direction: column;
			flex-wrap: nowrap;
			justify-content: space-between;
			align-items: center;
			@media only screen and (min-width: 768px) {
				background-size: contain;
				padding: 18px 25px 18px 25px;
			}

			&__box-header {
				padding: 0 10px 0 10px;
				margin-top: 10px;
				display: flex;
				height: 35px;
				width: 100%;
				@media only screen and (min-width: 768px) {
					padding: 0;
					height: 48px;
				}
				& figure {
					display: flex;
					flex-direction: column;
					flex-wrap: wrap;
					align-items: flex-start;
					justify-content: center;
					& img {
						height: 30px;
						width: 30px;
						margin-right: 8px;
						@media only screen and (min-width: 768px) {
							margin-right: 16px;
							padding: 0;
							height: 48px;
							width: 48px;
						}
					}

					& span {
						font-style: normal;
						font-weight: 600;
						font-size: 10px;
						line-height: 14px;
						color: #1a010c;
						@media only screen and (min-width: 768px) {
							font-size: 15px;
							line-height: 18px;
						}
					}

					& figcaption {
						display: flex;
						flex-direction: column;
						flex-wrap: wrap;
						margin-top: 2px;
						font-style: normal;
						font-weight: 300;
						font-size: 9px;
						line-height: 14px;
						color: #8c8c8c;
						@media only screen and (min-width: 768px) {
							font-size: 13px;
							line-height: 18px;
						}
					}
				}
			}

			&__box-footer {
				margin-bottom: 10px;
				z-index: 2;
				display: flex;
				justify-content: center;
				align-items: center;
				background-color: white;
				border-radius: 10px;
				height: 28px;
				width: 90%;
				font-style: normal;
				font-weight: 350;
				font-size: 9px;
				@media only screen and (min-width: 768px) {
					width: 265px;
					height: 48px;
				}
				& figure {
					display: flex;
					justify-content: center;
					align-items: center;
					align-content: center;
					width: calc(75% / 3);
					height: 16px;
					img {
						width: 16px;
						height: 16px;
						object-fit: cover;
					}
					@media only screen and (min-width: 768px) {
						font-style: normal;
						font-weight: 500;
						font-size: 12px;
						line-height: 15px;
						height: 24px;
						& img {
							width: 24px;
							height: 24px;
						}
					}
				}
				figure:nth-of-type(1) {
					align-items: center;
					font-style: normal;
					font-weight: 500;
					font-size: 10px;
					img {
						width: 12px;
						height: 11px;
						margin-left: 2px;
						margin-bottom: 2px;
					}
					@media only screen and (min-width: 768px) {
						font-weight: 600;
						font-size: 16px;
						line-height: 20px;
						height: 24px;
						& img {
							width: 18px;
							height: 16px;
						}
					}
				}
				figure:nth-of-type(2) {
					width: 40%;
					font-size: 8px;
					font-style: normal;
					font-weight: 500;
					figcaption {
						margin-left: 2px;
					}
					@media only screen and (min-width: 768px) {
						font-weight: 500;
						font-size: 12px;
						line-height: 15px;
					}
				}
			}
		}

		&-four {
			background: {
				color: #cbd0fe;
				image: url('/assets/backgroundFourCard.png');
				position: 0 150%;
				repeat: no-repeat;
				size: 175px;
			}
			position: relative;
			z-index: 1;
			overflow: visible;
			@media only screen and (min-width: 768px) {
				background-size: cover;
			}
			& img {
				position: absolute;
				bottom: 0;
				z-index: 2;
				right: 30px;
				max-block-size: 150%;
				max-width: 120%;
			}
		}
	}
	&-container-happyUser {
		display: flex;
		justify-content: flex-start;
		width: 100%;
		&-box {
			margin: 40px 16px 31px 16px;
			@media only screen and (min-width: 1206px) {
				margin: 40px 0px 31px 0px;
			}
			width: 305px;
			height: 44px;
			display: flex;
			flex-direction: row;
			flex-wrap: nowrap;
			align-content: center;
			& img {
				height: 44px;
				margin-right: 16px;
			}
			& figure {
				display: flex;
				justify-content: center;
				align-items: flex-start;
				flex-direction: column;
				flex-wrap: nowrap;
				width: 177px;

				& span {
					font-style: normal;
					font-weight: 500;
					font-size: 16px;
					line-height: 20px;
				}
				& figcaption {
					display: flex;
					height: 24px;
					font-style: normal;
					font-weight: 300;
					font-size: 14px;
					line-height: 24px;
					& img {
						width: 24px;
						height: 24px;
						margin-right: 4px;
					}
				}
			}
		}
	}
}
