.section-detailsVideo {
	display: flex;
	justify-content: center;
	min-width: 375px;
	&-container {
		width: 100%;
		max-width: 1210px;
		padding: 48px 0px 0px 0px;
		@media only screen and (min-width: 1024px) {
			padding: 48px 0px 83px 0px;
			display: flex;
			justify-content: center;
			flex-direction: column;
			align-items: center;
		}
		&-title {
			border: 1px solid blue;
			padding: 0px 28px 0px 28px;
			@media only screen and (min-width: 1024px) {
				padding: 48px 0px 0px 0px;
			}
			& h4 {
				//padding: 0 50px 0 50px;
				font-weight: 300;
				font-size: 28px;
				line-height: 34px;
				text-align: center;

				color: #030528;
				margin-bottom: 24px;

				& b {
					font-weight: 600;
					font-size: 28px;
					line-height: 34px;

					color: #d00561;
				}
			}

			& p {
				padding: 0 10px 0 10px;
				font-style: normal;
				font-weight: 300;
				font-size: 18px;
				line-height: 22px;
				text-align: center;

				color: #030528;
				margin-bottom: 64px;

				& b {
					font-style: normal;
					font-weight: 600;
					font-size: 18px;
					line-height: 22px;

					color: #030528;
				}
			}
		}

		&-playlist {
			padding: 0px 28px 0px 28px;
			@media only screen and (min-width: 1024px) {
				padding: 0px 0px 0px 0px;
			}
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			justify-content: space-between;
			width: 100%;
			max-width: 1210px;
			@media only screen and (min-width: 1024px) {
				display: flex;
				justify-content: space-between;
			}
			&__box {
				width: 100%;
				@media only screen and (min-width: 1024px) {
					display: flex;
					justify-content: space-between;
					gap: 8px;
					order: 2;
					width: auto;
				}
				&-item {
					border: 1px solid #fdb6d7;
					border-radius: 12px;
					height: 64px;
					width: 100%;
					display: flex;
					justify-content: center;
					align-items: center;
					margin-bottom: 8px;
					opacity: 0.5;
					order: 2;
					&__active {
						opacity: 1;
					}
					@media only screen and (min-width: 1024px) {
						width: 400px;
						margin-bottom: 0px;
					}

					&__text {
						font-style: normal;
						font-weight: 600;
						font-size: 16px;
						line-height: 20px;
						color: #3e4954;
						margin: 0 16px 0 15px;
					}

					&__counter {
						margin-left: 9px;
						font-style: normal;
						font-weight: 400;
						font-size: 14px;
						line-height: 20px;

						color: #41506c;
					}
				}
			}
			&__switch {
				padding: 0 2px 0 2px;
				margin-top: -8px;
				display: flex;
				justify-content: center;
				align-items: center;
				flex-direction: row;
				width: 32px;
				height: 64px;
				@media only screen and (min-width: 1024px) {
					margin-top: 0px;
				}
				&:nth-of-type(1) {
					order: 1;
				}
				&:nth-of-type(2) {
					order: 4;
					svg{
						fill: #D00561;
					}
				}
				&-svg {
					fill: #fdb6d7;
				}
			}
		}

		&-video {
			padding: 0px 28px 0px 28px;
			background: #effbf7;
			display: flex;
			justify-content: center;
			align-items: center;
			position: relative;
			width: 100%;
			@media only screen and (min-width: 1024px) {
				padding: 0px 0px 0px 0px;
				margin-top: 117px;
				height: 496px;
				max-width: 1210px;
			}
			& div {
				display: flex;
				width: 100%;
				@media only screen and (min-width: 768px) {
					width: 768px;
				}
				@media only screen and (min-width: 1024px) {
					width: 911px;
					height: 512px;
					position: absolute;
					top: -145px;
				}
				& img {
					margin: 48px 0px 48px 0px;
					width: 100%;
					border-radius: 12px;
					@media only screen and (min-width: 1024px) {
						width: 911px;
						height: 512px;
					}
				}
			}
//			@media only screen and (min-width: 1024px) {
